import { hot } from "react-hot-loader/root"
import React from "react"
import { PointPrimitive } from "resium"
import { Cartesian3, Color} from "cesium";
class GloboComponente extends React.Component{
	constructor( props){
		super(props)
		this.state={
			colore:Color.ORANGE,
			props1:props,
			id: props.dati.id
		}
	}
	componentDidMount(){
		if(this.props.idPrem === this.state.id){
			if(this.state.colore === Color.ORANGE){
				this.setState({
					colore: Color.RED
				})
				return true	
			}
			
		}
	}
	shouldComponentUpdate(nextProps){
		if(nextProps.idPrem === this.state.id){
			if(this.state.colore === Color.ORANGE){
				this.setState({
					colore: Color.RED
				})
				return true	
			}
			
		}	
		if(nextProps.idPrem  !== this.state.id){
			if(this.state.colore === Color.RED){
				this.setState({
					colore: Color.ORANGE
				})
				return true
			}
			
		}
			return false
		}
	
	render(){
	return(
			<PointPrimitive
	          color={this.state.colore}
	          position={Cartesian3.fromDegrees(this.state.props1.dati.longitudine, this.state.props1.dati.latitudine, 0)}
			  onClick={() => {this.state.props1.click(this.state.id)}}
			  onTouchStart={() => this.state.props1.click(this.state.id)}
			  
			  />
		)
	}
}
export default hot(GloboComponente)
