import React from "react"
import * as rssParser from 'react-native-rss-parser';
import { hot } from "react-hot-loader/root"
import "./rss.css"
import RssComponente from "./RssComponente"
import Loader from 'react-loader-spinner';
class RssFeed extends React.Component{
	constructor(props){
		super(props)
		this.state = {
		  loading: true,
	      rss: [],
	      rssfeed:[]
	  }
		
	}
	componentDidMount() {
		console.log("appena monato comp",this.props)
		let id=1
		if(this.props.nome !== undefined){
			console.log("vivo")
			id= this.props.nome.id
		}
		console.log("dopo monato comp",id)
        fetch('http://backend.ilbug.com/api/v1.0/rss/'+id) 
            .then(response => response.json())
            .then(data => {
            	const dati= data.map(x=>{return (x)})
                this.setState({
                    rss: dati, 
                }) 
                const rssfeedcom=[]
            this.state.rss.map(x=>(
            	fetch("https://cors-anywhere.herokuapp.com/"+x.url)
            	.then(response => response.text())
			    .then(responseData => rssParser.parse(responseData))
			    .then(datis => {rssfeedcom.push(datis.items.map(dati=>{return(dati)}))
			    	this.setState({
			    		loading: false,
			    		rssfeed: rssfeedcom
			    	})
			})
			    ))
			})
	}
	render(){
		console.log(this.props)
		let nome=null
		let url=null
		if(this.props.nome !== undefined){
			nome=this.props.nome.nome
			url=this.props.nome.url
		}
		const Rsscomponentes= this.state.rssfeed.map( d => {return(d.map((x,index)=>{return(
			<RssComponente key={index} dati={x} />)}))
	}) 
		let componenti=[]
		Rsscomponentes.map(((d) => {return d.map((x,index) => {return(componenti.push(x))})}))
		 		return(
		 		<div className="rettangolo">
						
				 <a href={url} target="_blank" rel="noopener noreferrer" id="h1s" >{nome}</a>
						{ this.state.loading ? <div style={{margin: "auto"}}> <Loader type="TailSpin" color="#2BAD60" height={50} width={50}  /> </div>   :  componenti  }	
								
				</div>


			)
		
}
}
export default hot(RssFeed)